import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class EditSchoolType extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className="pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Edit School Type </h1> </div>
        </div>

        
        <ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>

                <li className="active"> <Link className="parent-item" to="/school-types"> School Types </Link><i className="fa fa-angle-right"></i> </li>
                <li className="active">Edit School Type</li>
							</ol>


      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>Editing</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">

                      <div className="form-group row">
              				<label htmlFor="title" className="col-sm-2 col-form-label text-right">Title :
              				</label>
              					<div className="col-sm-10">
              						<input id="title" name="title" type="text"
              						placeholder="Title " className="form-control required" required value="Type A"/>
              					</div>
                      </div>
                      <div className="form-group row">
              				<label htmlFor="code" className="col-sm-2 col-form-label text-right">Name :
              				</label>
              					<div className="col-sm-10">
              						<input id="Code" name="code" type="text"
              						placeholder="Code " className="form-control required" required value="Card System"/>
              					</div>
                      </div>
                      <div className="form-group row">
              				<label for="email" className="col-sm-2 col-form-label text-right">Description :
              				</label>
              					<div className="col-sm-10">
              						<textarea name="description" rows="8"  className="form-control"> School that uses card as a mode of application</textarea>
              					</div>
                      </div>


						<div id="button">
							
														<button className="btn btn-info" >Save </button>

                            
						</div>


                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>


        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }