import React, { Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import ReactSummernote from 'react-summernote';
import 'react-summernote/dist/react-summernote.css';
import 'bootstrap';
import { Link } from 'react-router-dom';
import data from './constants';  
import Loading from './loader';

window.jquery = $;



export default class AddAPI extends Component {
     constructor(props) {
         super(props);
		 this.onChangeTitle = this.onChangeTitle.bind(this);
         this.onChangePurpose = this.onChangePurpose.bind(this);
		 this.onChangeUrl = this.onChangeUrl.bind(this);
		 this.onChangeHost = this.onChangeHost.bind(this);
		 this.onChangeVersion = this.onChangeVersion.bind(this);
		 this.onChangeSchool = this.onChangeSchool.bind(this);
		 
         this.onSubmit = this.onSubmit.bind(this); 
 
         this.state = {
             title: '',
             purpose: '',
             url: '',
			 host: '',
			 version: '',
             error:'',
			 button: 'Send Message',
			 schoolId: '0',
			 loading: false,
			 schools: []
         }
     } 

   
	 componentDidMount() {
		
		axios.get(`${data.host}api/v1/schools?token=${data.token}`)
		.then(response => {
			this.setState({schools: response.data})
		})
		.catch((error) => {
		  console.log(error);
		});
		}

		onChangeSchool(e) {
			this.setState({schoolId: e.target.value})
		}

		onChangePurpose(e) {
			this.setState({purpose: e});
		  }
	   
		  onChangeTitle(e) {
		   this.setState({title: e.target.value});
		 }
	   
		 onChangeUrl(e) {
		   this.setState({url: e.target.value});
		 }

		 onChangeHost(e) {
			this.setState({host: e.target.value});
		  }

		  onChangeVersion(e) {
			this.setState({version: e.target.value});
		  }
	   
		 onSubmit(e) {
		   e.preventDefault()
		   this.setState({loading: true, error: ''});
	   
		   const details = {
			 title: this.state.title,
			 purpose: this.state.purpose,
			 url: this.state.url,
			 host: this.state.host,
			 version: this.state.version,
			 schoolId: this.state.schoolId
		   }
	   
		   axios.post(`${data.host}api/v1/apis/add?token=${data.token}`, details)
			 .then(res => {
			   window.history.back();
			 }).catch(err => {
			   this.setState({ loading: false, error: err.toString() });
			 });
	   
		 }

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">New API</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/api-manager">API Manager</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">New API</li>
							</ol>
						</div>
					</div>
					<div className="inbox">
						<div className="row">
							<div className="col-md-12">
								<div className="card card-topline-gray">
									<div className="card-body no-padding height-9">
										<div className="row">

											<div className="col-md-12">
												<div className="inbox-body">

													<div className="inbox-body no-pad">

															<div className="compose-mail">
																<form onSubmit={this.onSubmit}>
																<div>
																	<div style={{marginLeft:"-15px"}} className="col-md-12">
																		Select School
                    </div>
																	<div style={{marginLeft:"-15px"}} className="col-md-12">
																		<select onChange={this.onChangeSchool} className="form-control" name="school" style={{ width: "600px", margin: "0px;" }}>
																			<option value="0">Control house</option>
																			{this.state.schools.map((school) => <option value={school.id}>{school.title}</option>)}
																			
																		</select>
																	</div>
																</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Title:</label>
																		<input onChange={this.onChangeTitle} value={this.state.title} type="text" tabindex="1" id="title"
																			className="form-control" required/>
																	</div>

																	<div className="form-group">
																		<label htmlFor="subject" className="">Domain host:</label>
																		<input onChange={this.onChangeHost} value={this.state.host} type="text" tabindex="1" id="title"
																			className="form-control" required/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">API URL:</label>
																		<input onChange={this.onChangeUrl} value={this.state.url} type="text" tabindex="1" id="title"
																			className="form-control" required/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Version:</label>
																		<input onChange={this.onChangeVersion} value={this.state.version} type="text" tabindex="1" id="title"
																			className="form-control" required/>
																	</div>
																	<div className="compose-editor" >
																			<label htmlFor="subject" className="">Purpose:</label>
																	<ReactSummernote
																		value={this.state.purpose}
																		options={{
																			height:180,
																			dialogsInBody: true,
																			toolbar: [
																				['style', ['style']],
																				['font', ['bold', 'underline', 'clear']],
																				['fontname', ['fontname']],
																				['para', ['ul', 'ol', 'paragraph']],
																				['table', ['table']],
																				['insert', ['link', 'picture', 'video']],
																				['view', ['fullscreen', 'codeview']]
																			]
																		}}
																		onChange={this.onChangePurpose}
																	/>
																	</div>
																	<div className="text-center p-t-30">
																	<p className="txt1">
																		{this.state.error}
																	</p>
																</div>
																	<div className="btn-group margin-top-20 ">
																		{ this.state.loading? this.Loaderview() :<> <button type="submit"
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Submit</button>
																		<Link to="/api-manager"><button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			Cancel</button> </Link> </>}
																	</div>

																</form>
															</div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

            )
        }
    }