import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class Profile extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">User Profile</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/users/profile/873">Profile</Link>&nbsp;<i
										className="fa fa-angle-right"></i>
								</li>
								<li className="active">User Profile</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12">
							
							<div className="profile-sidebar">
								<div className="card card-topline-aqua">
									<div className="card-body no-padding height-9">
										<div className="row">
											<div className="profile-userpic">
												<img src="/assets/img/dp.jpg" className="img-responsive" alt="..."/> </div>
										</div>
										<div className="profile-usertitle">
											<div className="profile-usertitle-name">Umaru Abbas Dikko </div>
											<div className="profile-usertitle-job"> Professor </div>
										</div>
										<ul className="list-group list-group-unbordered">
											<li className="list-group-item">
												<b>Followers</b> <a href="#/" className="pull-right">1,200</a>
											</li>
											<li className="list-group-item">
												<b>Following</b> <a href="#/" className="pull-right">750</a>
											</li>
											<li className="list-group-item">
												<b>Friends</b> <a href="#/" className="pull-right">11,172</a>
											</li>
										</ul>
										
										<div className="profile-userbuttons">
											<Link to="/messages/compose"><button type="button" className="btn btn-circle red btn-sm">Message</button></Link>
										</div>
										
									</div>
								</div>
								<div className="card">
									<div className="card-head card-topline-aqua">
										<header>About Me</header>
									</div>
									<div className="card-body no-padding height-9">
										<div className="profile-desc">
											Hello I am Umaru Abbas Dikko a web and user interface designer. I love to work
											with the application interface and
											the web elements.
										</div>
										<ul className="list-group list-group-unbordered">
											<li className="list-group-item">
												<b>Gender </b>
												<div className="profile-desc-item pull-right">Male</div>
											</li>
											<li className="list-group-item">
												<b>Project Done </b>
												<div className="profile-desc-item pull-right">30+</div>
											</li>
											<li className="list-group-item">
												<b>Skills</b>
												<div className="profile-desc-item pull-right">Java,Spring</div>
											</li>
										</ul>
										<div className="row list-separated profile-stat">
											<div className="col-md-4 col-sm-4 col-6">
												<div className="uppercase profile-stat-title"> 37 </div>
												<div className="uppercase profile-stat-text"> Projects </div>
											</div>
											<div className="col-md-4 col-sm-4 col-6">
												<div className="uppercase profile-stat-title"> 51 </div>
												<div className="uppercase profile-stat-text"> Tasks </div>
											</div>
											<div className="col-md-4 col-sm-4 col-6">
												<div className="uppercase profile-stat-title"> 61 </div>
												<div className="uppercase profile-stat-text"> Uploads </div>
											</div>
										</div>
									</div>
								</div>

							</div>
							
							<div className="profile-content">
								<div className="row">
									<div className="col-md-12">
										<div className="card">
											<div className="card-head card-topline-aqua">
												<header>UPDATE PROFILE</header>
											</div>
											<div className="card-body no-padding height-9">
												<div className="container-fluid">
													<div className="row">
														<div className="col-md-12">
															<div className="panel">
																<form>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Full Name:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control" value="Umaru Abbas Dikko"/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Rank:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control" value="Professor"/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Gender:</label>
																		<select className="form-control" name="gender" id="gender">
																			<option value="0">Select</option>
																			<option value="1">Male</option>
																			<option value="2">Female</option>
																		</select>
																	</div>
																	<div className="form-group">
																		<label for="subject" className="">Skills:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control" value="Java,Spring"/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Project Done:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control" value="30+"/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">About Me:</label>
																		<textarea className="form-control p-text-area" rows="4"
																			placeholder="Tell us what we should know about you and whats in your mind today?"></textarea>
																	</div>

																</form>
																<footer className="panel-footer">
																	<button
																		className="btn btn-info pull-right">Update Profile</button>
																	<ul className="nav nav-pills p-option">
																		<li>
																			<a href="#/"><i className="fa fa-user"></i></a>
																		</li>
																		<li>
																			<a href="#/"><i className="fa fa-camera"></i></a>
																		</li>
																		<li>
																			<a href="#/"><i
																					className="fa  fa-location-arrow"></i></a>
																		</li>
																		<li>
																			<a href="#/"><i className="fa fa-meh-o"></i></a>
																		</li>
																	</ul>
																</footer>
															</div>
														</div>
													</div>
													<div className="row">
														<div className="col-md-12">

														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
								
							</div>
						</div>
					</div>
				</div>

			</div>

            )
        }
    }