import React, { Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import JSZip from 'jszip';
import pdfMake from 'pdfmake';
import pdfFonts from "pdfmake/build/vfs_fonts";
import 'datatables.net-bs4';
import 'datatables.net-responsive';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.colVis'
import 'datatables.net-buttons/js/buttons.html5'
import 'datatables.net-buttons/js/buttons.flash'
import 'datatables.net-buttons/js/buttons.print'
import { Link } from 'react-router-dom';
import data from './constants';
import Loading from './loader';

window.JSZip = JSZip;

pdfMake.vfs = pdfFonts.pdfMake.vfs;



export default class APIManager extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
         this.onChangeSchool = this.onChangeSchool.bind(this);
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: true,
             apis: [],
             schools: [],
         }
     } 

   
       
     componentDidMount() {
      axios.get(`${data.host}api/v1/apis?token=${data.token}`)
      .then(response => {
        //console.log(Object.keys(this.state.adminTypes).map((key) => {return this.state.adminTypes[key]}))
        this.setState({ apis: response.data, loading: false });
  
        $(document).ready(() => {
        $('#exportTable').DataTable({
            lengthChange: true,
            dom: 'Bfrtip',
            data: response.data,
            columns: [
              { data: "id" },
              { data: "title" },
              { data: "purpose" },
              { data: "url" },
              { data: "host"},
              { data: "version"},
              { data: "active"},
              { data: "date_added" },
              { data: "id",
              render:  ( id ) => {
                return "<a href='/api-manager/edit-API/"+id+"' class='btn btn-info btn-xs delete_school' title='Edit record'><i class='fa fa-edit'></i></a> | <a href='/api-manager/activate-API/"+id+"' class='btn btn-primary btn-xs delete_school' title='Activate'><i class='fa fa-plus'></i></a>  | <a href='/api-manager/deactivate-API/"+id+"' class='btn btn-primary btn-xs delete_school' title='Deactivate'><i class='fa fa-lock'></i></a>";
                }}
              ],
            buttons: [
              'copy', 'csv', 'excel', 'pdf', 'print'
            ]
    
          });
          
        });
    
      })
      .catch((error) => {
        console.log(error);
      });
  
      axios.get(`${data.host}api/v1/schools?token=${data.token}`)
      .then(response => {
        this.setState({schools: response.data})
      })
      .catch((error) => {
        console.log(error);
      });
    
     }
  
   onChangeSchool(e) {
      // eslint-disable-next-line
     $('#exportTable').dataTable().fnDestroy();
     if (e.target.value !== '0000') {
     
      $('#exportTable').DataTable({
        lengthChange: true,
        dom: 'Bfrtip',
        data: this.state.apis.filter(api => api.school_id === e.target.value),
        columns: [
          { data: "id" },
          { data: "title" },
          { data: "purpose" },
          { data: "url" },
          { data: "host"},
          { data: "version"},
          { data: "active"},
          { data: "date_added" },
          { data: "id",
          render:  ( id ) => {
            return "<a href='/api-manager/edit-API/"+id+"' class='btn btn-info btn-xs delete_school' title='Edit record'><i class='fa fa-edit'></i></a> | <a href='/api-manager/activate-API/"+id+"' class='btn btn-primary btn-xs delete_school' title='Activate'><i class='fa fa-plus'></i></a>  | <a href='/api-manager/deactivate-API/"+id+"' class='btn btn-primary btn-xs delete_school' title='Deactivate'><i class='fa fa-lock'></i></a>";
            }}
          ],
        buttons: [
          'copy', 'csv', 'excel', 'pdf', 'print'
        ]

      });
        
     }else{
      $('#exportTable').DataTable({
        lengthChange: true,
        dom: 'Bfrtip',
        data: this.state.apis,
        columns: [
          { data: "id" },
          { data: "title" },
          { data: "purpose" },
          { data: "url" },
          { data: "host"},
          { data: "version"},
          { data: "active"},
          { data: "date_added" },
          { data: "id",
          render:  ( id ) => {
            return "<a href='/api-manager/edit-API/"+id+"' class='btn btn-info btn-xs delete_school' title='Edit record'><i class='fa fa-edit'></i></a> | <a href='/api-manager/activate-API/"+id+"' class='btn btn-primary btn-xs delete_school' title='Activate'><i class='fa fa-plus'></i></a>  | <a href='/api-manager/deactivate-API/"+id+"' class='btn btn-primary btn-xs delete_school' title='Deactivate'><i class='fa fa-lock'></i></a>";
            }}
          ],
        buttons: [
          'copy', 'csv', 'excel', 'pdf', 'print'
        ]

      });
     }
      
   }

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">API Manager</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								
								<li className="active">API Manager</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12 col-sm-12">
							<div className="card card-box">
								<div className="card-head">
									<header>All APIs</header>

								</div>
								<div className="row">
									<div className="col-md-6 col-sm-6 col-6">
										<div className="btn-group">
											<Link to="/api-manager/add-api" id="addRow" className="btn btn-info" >
												Add New <i className="fa fa-plus"></i>
											</Link>
										</div>
									</div>

								</div>
								<div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
									<form className="form-inline">
                  <div style={{marginRight:"-25px"}}>
                    <div className="col-md-12">
                    Schools
                    </div>
                      <div className="col-md-12">
                    <select onChange={this.onChangeSchool} className="form-control" name="school" style={{width:"600px", margin:"0px;"}}>
                    <option value="0000">All APIs</option>
                      <option value="0">Control house</option>
                      {this.state.schools.map((school) => <option value={school.id}>{school.title}</option>)}
                    </select>
                  </div>
                  </div>



                </form>
									<table id="exportTable" className="user display table-bordered table-hover table-striped" cellSpacing="0" width="100%" style={{fontSize:"12px"}}>
										<thead>
											<tr>
                        <td>ID</td>
												<th>NAME</th>
												<th>PURPOSE</th>
												<th>URL</th>
                        <th>HOST</th>
												<th>VERSION</th>
                        <th>ACTIVE</th>
												<th>DATE ADDED</th>
												<th>ACTION</th>
											</tr>
										</thead>
									
										
									</table>
                  <div className="text-center p-t-30">
										<p className="txt1">
											{this.state.error}
										</p>
									</div>
                  {this.Loaderview()}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }