import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import JSZip from 'jszip';
import pdfMake from 'pdfmake';
import pdfFonts from "pdfmake/build/vfs_fonts";
import 'datatables.net-bs4';
import 'datatables.net-responsive';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.colVis'
import 'datatables.net-buttons/js/buttons.html5'
import 'datatables.net-buttons/js/buttons.flash'
import 'datatables.net-buttons/js/buttons.print'
import { Link } from 'react-router-dom';

window.JSZip = JSZip;

pdfMake.vfs = pdfFonts.pdfMake.vfs;


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class Programmes extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    
  componentDidMount() {
	    $(document).ready(function () {
        $('#exportTable').DataTable({
          lengthChange: true,
        dom: 'Bfrtip',
        buttons: [
            'copy', 'csv', 'excel', 'pdf', 'print'
        ]
		
    });
		});
   }
     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px;"}}>Programme List</h1> </div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
          <li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
              to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>

          <li className="active"> Programmes</li>
        </ol>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>All Programmes</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">
                      <div className="row">
                        <div className="col-md-6 col-sm-6 col-6">
                          <div className="btn-group">
                            <Link to="/programmes/add-programme" id="addRow" className="btn btn-info" >
                              Add New <i className="fa fa-plus"></i>
                            </Link>
                          </div>
                        </div>

                      </div>
                      <div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
                        <form className="form-inline">
                        <div style={{marginRight:"-25px"}}>
                          <div className="col-md-12">
                          Schools
                          </div>
                            <div className="col-md-12">
                          <select className="form-control" name="school" style={{width:"600px", margin:"0px"}}>
                            <option value="0">select school</option>
                            <option value="1">IBBUL</option>
                            <option value="2">NSUK</option>
                            <option value="3">COE</option>
                            <option value="4">FUT Mx</option>
                          </select>
                        </div>
                        </div>


                      <div style={{marginRight:"-25px"}}>

                          <div className="col-md-12" style={{marginTop:"25px;"}}>
                      <button type="submit" className="btn btn-default" style={{width:"80px"}}>Search</button>
                      </div>
                    </div>

                      </form>
                        <table id="exportTable" className="display nowrap" style={{width:"100%"}}>
                          <thead>
                            <tr>
                              <th></th>

                              <th> Title </th>
                              <th> Code </th>
                              <th> School </th>
                              <th> Faculty </th>
                              <th> Department </th>

                              <th> Action </th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr className="odd gradeX">
                              <td className="left">
                              1
                              </td>
                              <td className="left">Bsc. Computer Science</td>
                              <td className="left">Bsc</td>
                              <td className="left">Undergraduate</td>

                              <td>Faculty of Natural Science</td>
                              <td>Computer Science</td>


                              <td>
                                <Link to="/programmes/edit-programme/9863"
                                  className="btn btn-primary btn-xs edit_school" title="Edit">
                                  <i className="fa fa-pencil"></i>
                                </Link>
                                <button className="btn btn-danger btn-xs delete_school" title="delete">
                                  <i className="fa fa-trash-o "></i>
                                </button>
                              </td>
                            </tr>


                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              </div>
              </div>
              </div>
              </div>
              </div>
              </div>
              </div>

            )
        }
    }