import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import JSZip from 'jszip';
import pdfMake from 'pdfmake';
import pdfFonts from "pdfmake/build/vfs_fonts";
import 'datatables.net-bs4';
import 'datatables.net-responsive';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.colVis'
import 'datatables.net-buttons/js/buttons.html5'
import 'datatables.net-buttons/js/buttons.flash'
import 'datatables.net-buttons/js/buttons.print'
import { Link } from 'react-router-dom';

window.JSZip = JSZip;

pdfMake.vfs = pdfFonts.pdfMake.vfs;


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class SMS extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

     componentDidMount() {
	    $(document).ready(function () {
        $('#exportTable').DataTable({
          lengthChange: true,
        dom: 'Bfrtip',
        buttons: [
            'copy', 'csv', 'excel', 'pdf', 'print'
        ]
		
    });
		});
   }
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">Manage SMS</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>

								<li className="active">SMS</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12 col-sm-12">
							<div className="card card-box">
								<div className="card-head">
									<header>All SMS</header>

								</div>
								<div className="row">
									<div className="col-md-6 col-sm-6 col-6">
										<div className="btn-group" style={{marginLeft:"20px"}}>
											<Link to="/sms/new-sms" id="addRow" className="btn btn-info" >
												Send New SMS<i className="fa fa-plus"></i>
											</Link>
										</div>
									</div>

								</div>
								<div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
									<table id="exportTable" className="display nowrap" style={{width:"100%"}}>
										<thead>
											<tr>
                        <td></td>
												<th>SCHOOL</th>
												<th>RECEIVER TYPE</th>
												<th>RECEIVER</th>
												<th>SMS</th>
												<th>GATEWAY</th>
												<th>DATE ADDED</th>
												<th>ACTION</th>
											</tr>
										</thead>
										<tbody>




											<tr>
                        <td>1</td>
												<td>COE Minna</td>
												<td>Superadmin</td>
												<td>Aslapai</td>
												<td>testing</td>
												<td>bulk sms</td>
												<td>2020/08/06</td>
                        <td>
                          <Link to="/sms/edit-sms/432"
                            className="btn btn-primary btn-xs" title="Edit record">
                            <i className="fa fa-pencil"></i>
                          </Link>
                          <button className="btn btn-danger btn-xs delete_school" title="Delete record">
                            <i className="fa fa-trash-o "></i>
                          </button>
                        </td>
											</tr>
										</tbody>
									
									</table>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }