import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';
import Activity from './activity'


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class ControlManager extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Control Manager</h1> </div>
        </div>

      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>All Controls</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">
                      <div className="col-md-12">
                        <div className="col-md-6" style={{float:"left"}}>
                          <div className="panel panel-default" >
                            <div className="panel-heading" style={{background:"#353c48", borderColor:"black;"}}>Quick Links</div>
                            <div className="panel-body" style={{marginLeft:"-10px"}}>
                              <div className="row">
                                <div >
                                  <label style={{backgroundColor:"#292F33", width:"65px", height:"60px", margin:"10px", marginLeft: "20px", borderRadius:"5px"}} title="Mass Mailing"><Link to="/email/mass-mail"><img src="assets/svg/regular/envelope.svg" title="Mass Mailing" style={{width:"65px", height:"60px"}} alt="..." className="img-rounded"/></Link></label>
                                </div>
                                <div >
                                  <label style={{backgroundColor:"#292F33", width:"65px", height:"60px", margin:"10px", marginLeft: "20px", borderRadius:"5px"}} title="Manage Users"><Link to="/users"><img src="assets/svg/regular/user.svg" title="Manage Users" style={{width:"65px", height:"60px"}} alt="..." className="img-rounded"/></Link></label>
                                </div>
                                <div >
                                  <label style={{backgroundColor:"#292F33", width:"65px", height:"60px", margin:"10px", marginLeft: "20px", borderRadius:"5px"}} title="Manage News"><Link to="/news"><img src="assets/svg/regular/newspaper.svg" style={{width:"65px", height:"60px"}} title="Manage News" alt="..." className="img-rounded"/></Link></label>
                                </div>
                                <div >
                                  <label style={{backgroundColor:"#292F33", width:"65px", height:"60px", margin:"10px", marginLeft: "20px", borderRadius:"5px"}} title="Manage Messages"><Link to="/messages/inbox"><img src="assets/svg/regular/comment.svg" title="Manage Messages" style={{width:"65px", height:"60px"}} alt="..." className="img-rounded"/></Link></label>
                                </div>
                                <div >
                                  <label style={{backgroundColor:"#292F33", width:"65px", height:"60px", margin:"10px", marginLeft: "20px", borderRadius:"5px"}} title="Manage Notice"><Link to="/notices"><img src="assets/svg/regular/star.svg" style={{width:"65px", height:"60px"}} alt="..." className="img-rounded" title="Manage Notice"/></Link></label>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-md-6" style={{float:"right"}}>
                          <Activity />
                        </div>
                      </div>

                    </div>
                  </div>
                </div>





              </div>
            </div>






          </div>
        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }