import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class NewSMS extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">New SMS</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/sms">SMS</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">New SMS</li>
							</ol>
						</div>
					</div>
					<div className="inbox">
						<div className="row">
							<div className="col-md-12">
								<div className="card card-topline-gray">
									<div className="card-body no-padding height-9">
										<div className="row">

											<div className="col-md-12">
												<div className="inbox-body">

													<div className="inbox-body no-pad">

															<div className="compose-mail">
																<form>
																	<div className="form-group">
																		<label htmlFor="to" className="">School:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select School</option>
																			<option value="1">COE</option>
																			<option value="2">IBBUL</option>
																			<option value="3">NSUK</option>
																			<option value="4">FUT Mx</option>
																			<option value="5">School of Health</option>
																		</select>

																	</div>

																	<div className="form-group">
																		<label htmlFor="subject" className="">Receiver Type:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select Receiver type</option>
																			<option value="1">Superadmin</option>
																			<option value="2">Admin</option>
																			<option value="3">Schools</option>
																			<option value="4">Staff</option>
																			<option value="5">Student</option>
																		</select>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Receiver:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select Receiver</option>
																			<option value="1">Aslapai</option>
																			<option value="2">Abbas</option>
																			<option value="3">John</option>
																			<option value="4">Ezra</option>
																			<option value="5">Adamu</option>
																		</select>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Gateway:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select gateway</option>
																			<option value="1">Bulk sms</option>
																			<option value="2">normal</option>

																		</select>
																	</div>
																	<div className="compose-editor">
																			<label htmlFor="subject" className="">Notice:</label>
																		<div id="summernote"></div>
																	</div>

																	<div className="btn-group margin-top-20 ">
																		<button
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Send</button>
																		<Link to="/sms"><button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			Cancel</button></Link>
																	</div>

																</form>
															</div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

            )
        }
    }