import React, { Component } from 'react';
import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import JSZip from 'jszip';
import pdfMake from 'pdfmake';
import pdfFonts from "pdfmake/build/vfs_fonts";
import 'datatables.net-bs4';
import 'datatables.net-responsive';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.colVis'
import 'datatables.net-buttons/js/buttons.html5'
import 'datatables.net-buttons/js/buttons.flash'
import 'datatables.net-buttons/js/buttons.print'
import { Link } from 'react-router-dom';
import data from './constants'

window.JSZip = JSZip;

pdfMake.vfs = pdfFonts.pdfMake.vfs;


const Loading = (props) => (
    <div
    style={{
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center" 
    }}
  >
    <Loader type="Circles" color="#FFFFFF" height="40" wIDth="40" />
    <div className="text-center p-t-30">
						<p className="txt1">
							Please wait...
						</p>
					</div>
  </div>
  //
)




export default class AdminTypes extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
		 this.onSubmit = this.onSubmit.bind(this); */
		 this.onDeleteType = this.onDeleteType.bind(this);
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
			 loading: true,
			 adminTypes: []
         }
     } 

       componentDidMount() {

		axios.get(`${data.host}api/v1/admin-types?token=${data.token}`)
		.then(response => {
			//console.log(Object.keys(this.state.adminTypes).map((key) => {return this.state.adminTypes[key]}))
			this.setState({ adminTypes: response.data, loading: false });

			$(document).ready(() => {
			$('#exportTable').DataTable({
					lengthChange: true,
					dom: 'Bfrtip',
					data: response.data,
					columns: [
						{ data: "id" },
						{ data: "title" },
						{ data: "title" },
						{ data: "description" },
						{ data: "date_added" },
						{ data: "id",
						render:  ( id ) => {
							return "<a href='/admin-types/edit-admin-type/"+id+"' class='btn btn-primary btn-xs' title='Edit record'><i class='fa fa-pencil'></i></a> | <a href='/admin-types/edit-admin-type/"+id+"' class='btn btn-primary btn-xs' title='Delete record'><i class='fa fa-trash-o'></i></a>";
						  }}
					  ],
					buttons: [
						'copy', 'csv', 'excel', 'pdf', 'print'
					]
	
				});
				
			});
	
		})
		.catch((error) => {
			console.log(error);
		});


	
   }

 

   onDeleteType(id) {
	   this.setState({loading: true});
	   const details = {id: id}
	axios.post(`${data.host}api/v1/admin-types/delete?token=${data.token}`, details)
		.then(response => {

			this.setState({ adminTypes: this.state.adminTypes.filter(el => el.id !== id), loading: false });

			$(document).ready(() => {
				$('#exportTable').DataTable({
					lengthChange: true,
					dom: 'Bfrtip',
					data: this.state.adminTypes.filter(el => el.id !== id),
					"columns": [
						{ "data": "id" },
						{ "data": "title" },
						{ "data": "title" },
						{ "data": "description" },
						{ "data": "date_added" },
						{ "data": <button>Click!</button>}
					  ],
					  "columnDefs": [ {
						"targets": -1,
						"data": null,
						"defaultContent": "<button>Click!</button>"
					} ],
					  
					buttons: [
						'copy', 'csv', 'excel', 'pdf', 'print'
						
					]
			
	
				});
				
			});
	
		})
		.catch((error) => {
			console.log(error);
		});
   }
 
     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">Admin Types</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								
								<li className="active">Admin Types</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12 col-sm-12">
							<div className="card card-box">
								<div className="card-head">
									<header>All types</header>

								</div>
								<div className="row">
									<div className="col-md-6 col-sm-6 col-6">
										<div className="btn-group" style={{marginLeft:"20px"}}>
											<Link to="/admin-types/add-admin-type" id="addRow" className="btn btn-info" >
												Add New <i className="fa fa-plus"></i>
											</Link>
										</div>
									</div>

								</div>
								<div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
									<table id="exportTable" className="user display table-bordered table-hover table-striped" cellSpacing="0" width="100%" style={{fontSize:"12px"}}>
										<thead>
											<tr>
                        <td>ID</td>
												<th>TITLE</th>
												<th>NAME</th>
												<th>DESCRIPTION</th>

												<th>DATE ADDED</th>
												<th>ACTION</th>
											</tr>
										</thead>
										
										
									</table>
									{this.Loaderview()}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }