import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class AddUserRoles extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Add Users Roles </h1> </div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
          <li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
              to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li><Link className="parent-item" to="/users">Users</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li className="active">Add User Roles</li>
        </ol>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>New Roles</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">
                      <div className="form-group col-md-12">
                      <label for="email">Title:</label>
                      <input type="text" name="f_name" placeholder="Title" className="form-control" value=""  required  />
                      </div>
                      <div className="form-group col-md-12">
                      <label for="email">Descriptions:</label>
                      <textarea name="name" rows="8" cols="80" className="form-control"></textarea>
                      </div>
                      <div className="form-group col-md-12">
                      <label htmlFor="email">Status:</label>
                      <select className="form-control" name="status">
                        <option value="0">-- Choose Status</option>
                        <option value="1">Unpublished</option>
                        <option value="2">Published</option>
                        <option value="3">Trashed</option>
                        <option value="4">Archived</option>
                      </select>
                      </div>





						<div id="button">
						
                <button
                  className="btn btn-primary btn-xs edit_school">
                  <i className="fa fa-pencil"> Submit</i>
                </button>



						</div>


                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>

        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }