import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class EditUser extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           
              <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">Edit User</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/users">Users</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								
								<li className="active">Edit user</li>
							</ol>
						</div>
					</div>
    <div className="row">
    
      <div className="col-md-12">
      
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>Editing</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">
                    <form>
                      <div className="form-group row">
        												<label className="col-sm-2 col-form-label text-right"> School</label>
        									<div className="col-sm-10">
        										<select className="form-control" name="school" id="school">
                              <option value="0">Select</option>
                              <option value="1">school one</option>
                              <option value="2">school two</option>
                            </select>
        									</div>
        							</div>

                      <div className="form-group row">
              				<label for="title" className="col-sm-2 col-form-label text-right">Image :
              				</label>
              					<div className="col-sm-10">
              						<input id="logo" name="logo" type="file"
              						placeholder="Logo " className="form-control required" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label for="title" className="col-sm-2 col-form-label text-right">Name :
              				</label>
              					<div className="col-sm-10">
              						<input id="title" name="title" type="text"
              						placeholder="Title " className="form-control required" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label for="code" className="col-sm-2 col-form-label text-right">Staff Code :
              				</label>
              					<div className="col-sm-10">
              						<input id="Code" name="code" type="text"
              						placeholder="Code " className="form-control required" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label for="email" className="col-sm-2 col-form-label text-right">Email
              				</label>
              					<div className="col-sm-10">
              						<input id="email" name="email" type="text"
              						placeholder="email " className="form-control required" />
              					</div>
                      </div>
                      <div className="form-group row">
        												<label className="col-sm-2 col-form-label text-right"> Gender</label>
        									<div className="col-sm-10">
        										<select className="form-control" name="school" id="school">
                              <option value="0">Select</option>
                              <option value="1">Male</option>
                              <option value="2">Female</option>
                            </select>
        									</div>
        							</div>

              			 <div className="form-group row">
              				<label for="location" className="col-sm-2 col-form-label text-right">Address
              				</label>
              					<div className="col-sm-10">
              						<input id="text" name="location" type="text"
              						placeholder="Location " className="form-control required"  />
              				</div>
                    </div>

            			 <div className="form-group row">
            				<label for="Phone Number" className="col-sm-2 col-form-label text-right">Phone Number
            				</label>
            					<div className="col-sm-10">
            						<input id="text" name="pn" type="text"
            						placeholder="Phone Number " className="form-control required"  />
            				</div>
                  </div>



						<div id="button">
							
							<Link to="/add-user" className="btn btn-info" >Add User </Link>

                            	<Link to="/users" className="btn btn-info" >View Users </Link>
						</div>

</form>
                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>



            

        </div>
      </div>
    </div>
  </div>
</div>
            )
        }
    }