import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class ErrorPage extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
             <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">Error 404 Not Found</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								
								<li className="active">Error 404</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12 col-sm-12">
							<div className="card card-box">
								<div className="card-head">
									<header>Error 404 Page Not Found</header>

								</div>
								<div className="row">


								</div>
								<div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
									<h2 className="title" style={{textAlign:"center", color:"red"}}>The page you're looking for doesn't exit on this server</h2>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
            )
        }
    }