import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import 'summernote';
import 'bootstrap';
import { Link } from 'react-router-dom';

window.jquery = $;

const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class EditAiInclination extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
     componentDidMount() {
		$(document).ready(function() {
			$('#summernote').summernote();
		  });
		}
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">Edit AI Inclination</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/ai-inclination">AI INCLINATION</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">Edit AI</li>
							</ol>
						</div>
					</div>
					<div className="inbox">
						<div className="row">
							<div className="col-md-12">
								<div className="card card-topline-gray">
									<div className="card-body no-padding height-9">
										<div className="row">

											<div className="col-md-12">
												<div className="inbox-body">

													<div className="inbox-body no-pad">

															<div className="compose-mail">
																<form>

																	<div className="form-group">
																		<label htmlFor="subject" className="">Name:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control" value="abbas" required/>
																	</div>

																	<div className="form-group">
																		<label htmlFor="subject" className="">Version:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control" value="2.0" required/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Date:</label>
																		<input type="date" tabindex="1" id="title"
																			className="form-control" value="2.0" required/>
																	</div>
																	<div className="compose-editor" >
																			<label htmlFor="subject" className="">Purpose:</label>
																		<div id="summernote" ></div>
																	</div>

																	<div className="btn-group margin-top-20 ">
																		<button type="submit"
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Submit</button>
																		<Link to="/ai-inclination"><button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			Cancel</button></Link>
																	</div>

																</form>
															</div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

            )
        }
    }