import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class AddUserPrivileges extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
               <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Add User Privileges </h1> </div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
          <li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
              to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li><Link className="parent-item" to="/users">Users</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li className="active">Add User Privileges</li>
        </ol>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>New Privilege</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">
                      <div className="form-group row">
        												<label className="col-sm-2 col-form-label text-right"> Users</label>
        									<div className="col-sm-10">
        										<select className="form-control" name="school" id="school">
                              <option value="0">Select</option>
                              <option value="1">Abbas</option>
                              <option value="2">John</option>
                            </select>
        									</div>
        							</div>

                      <div className="form-group row">
        												<label className="col-sm-2 col-form-label text-right"> Privileges</label>
        									<div className="col-sm-10">
                            <div className="col-md-12">
                              <div className="col-md-10">
                              1 &nbsp;&nbsp;&nbsp; <input type="checkbox"/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Add
                              </div>
                              <div className="col-md-10">
                              2 &nbsp;&nbsp; <input type="checkbox"/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  Edit
                              </div>
                              <div className="col-md-10">
                              3 &nbsp;&nbsp; <input type="checkbox"/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  Delete
                              </div>
                              <div className="col-md-10">
                              4 &nbsp;&nbsp; <input type="checkbox"/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  Suspend
                              </div>
                              <div className="col-md-10">
                              5 &nbsp;&nbsp; <input type="checkbox"/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;  Achived
                              </div>

                            </div>
        									</div>
        							</div>






						<div id="button">
							
                <button type="submit"
                  className="btn btn-primary btn-xs edit_school">
                  <i className="fa fa-pencil"> Assign</i>
                </button>


						</div>


                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>

        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }