import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class MessageSent extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">Send</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/messages/inbox">Message Inbox</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">Sent</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12">
							<div className="card card-topline-gray">
								<div className="card-body no-padding height-9">
									<div className="inbox">
										<div className="row">
											<div className="col-md-3">
												<div className="inbox-sidebar">
													<a href="/messages/compose" data-title="Compose"
														className="btn red compose-btn btn-block">
														<i className="fa fa-edit"></i> Compose </a>
													<ul className="inbox-nav inbox-divider">
														<li><Link to="/messages/inbox"><i
																	className="fa fa-inbox"></i> Inbox <span
																	className="label mail-counter-style label-danger pull-right">2</span></Link>
														</li>
														<li className="active"><Link to="/messages/sent"><i className="fa fa-envelope"></i> Sent
															<span
																className="label mail-counter-style label-info pull-right">0</span></Link>
														</li>
														<li><Link to="/messages/draft"><i className=" fa fa-external-link"></i> Drafts
																<span
																	className="label mail-counter-style label-info pull-right">0</span></Link>
														</li>
														<li><Link to="/messages/trash"><i className=" fa fa-trash-o"></i> Trash
															<span
																className="label mail-counter-style label-info pull-right">0</span></Link>
														</li>
													</ul>


												</div>
											</div>
											<div className="col-md-9">
												<div className="inbox-body">
													<div className="inbox-header">
														<div className="mail-option no-pad-left">
															<div className="btn-group group-padding">
																<a className="btn mini tooltips" href="#/"
																	data-toggle="dropdown" data-placement="top"
																	data-original-title="Refresh" title="refresh">
																	<i className=" fa fa-refresh fa-lg"></i>
																</a>
																<a className="btn mini tooltips" href="#/"
																	data-original-title="draft"> <i
																		className=" fa fa-archive fa-lg"></i>
																</a>
																<a className="btn mini tooltips" href="#/"
																	data-original-title="Trash"> <i
																		className=" fa fa-trash-o fa-lg"></i>
																</a>
															</div>


															<div className="btn-group pull-right btn-prev-next">
																<button className="btn btn-sm btn-primary" type="button">
																	<i className="fa fa-chevron-left"></i>
																</button>
																<button className="btn btn-sm btn-primary" type="button">
																	<i className="fa fa-chevron-right"></i>
																</button>
															</div>
														</div>
													</div>
													<div className="inbox-body no-pad table-responsive">
														<table className="table table-inbox table-hover">
															<tbody>
																<tr className="unread">
																	<td className="inbox-small-cells">
																		<div className="todo-check pull-left">
																			<input type="checkbox" value="None"
																				id="todo-check1"/>
																			<label for="todo-check1"></label>
																		</div>
																	</td>
																	<td className="inbox-small-cells"><i
																			className="fa fa-star inbox-started"></i>
																	</td>
																	<td>
																		<a href="#/" className="avatar">
																			<img src="/assets/img/std/std8.jpg"
																				alt=""/>
																		</a>
																	</td>
																	<td className="view-message  dont-show">Leena Smith</td>
																	<td className="view-message "><Link
																			to="/messages/view/412">Jatin I found you
																			on LinkedIn.</Link></td>
																	<td className="view-message  inbox-small-cells"><i
																			className="fa fa-paperclip"></i>
																	</td>
																	<td className="view-message  text-right">10:27 AM</td>
																</tr>
																<tr>
																	<td className="inbox-small-cells">
																		<div className="todo-check pull-left">
																			<input type="checkbox" value="None"
																				id="todo-check25"/>
																			<label for="todo-check25"></label>
																		</div>
																	</td>
																	<td className="inbox-small-cells"><i
																			className="fa fa-star-o"></i>
																	</td>
																	<td>
																		<a href="#/" className="avatar">
																			<img src="/assets/img/std/std4.jpg"
																				alt=""/>
																		</a>
																	</td>
																	<td className="view-message dont-show">Kehn Anderson
																	</td>
																	<td className="view-message view-message"><Link
																			to="/messages/view/932">Merry
																			Christmas</Link></td>
																	<td className="view-message inbox-small-cells"><i
																			className="fa fa-paperclip"></i>
																	</td>
																	<td className="view-message text-right">dec 14</td>
																</tr>
															</tbody>
														</table>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }