import React, { Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import data from './constants';
import Loading from './loader';

window.jquery = $;



export default class DeleteAppType extends Component {
     constructor(props) {
         super(props);
		 
         this.onSubmit = this.onSubmit.bind(this); 
 
         this.state = {
             title: '',
             purpose: '',
             url: '',
			 host: '',
			 version: '',
             error:'',
             button: 'Send Message',
             loading: false,
         }
     } 

   
	 componentDidMount() {}


	   
		 onSubmit(e) {
		   
		   this.setState({loading: true, error: ''});
	   
		   const details = {
			 id: this.props.match.params.id
		   }
	   
		   axios.post(`${data.host}api/v1/app-types/delete?token=${data.token}`, details)
			 .then(res => {
			   window.location = '/app-types';
			 }).catch(err => {
			   this.setState({ loading: false, error: err.toString() });
			 });
	   
		 }

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">Delete Application Type</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/app-types">Application Types</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">Delete Application Type</li>
							</ol>
						</div>
					</div>
					<div className="inbox">
						<div className="row">
							<div className="col-md-12">
								<div className="card card-topline-gray">
									<div className="card-body no-padding height-9">
										<div className="row">

											<div className="col-md-12">
												<div className="inbox-body">

													<div className="inbox-body no-pad">

															<div className="compose-mail">
                                                            <div className="text-center p-t-30">
																	<h2 className="txt1">
																		Are you sure you want to delete this Application Type?
																	</h2>
																	<div className="text-center p-t-30">
																	<p className="txt1">
																		{this.state.error}
																	</p>
																</div>
																	<div className="btn-group margin-top-20 ">
																		{ this.state.loading? this.Loaderview() :<> <button onClick={this.onSubmit}
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Yes</button>
																		<Link to="/api-manager"><button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			No</button> </Link> </>}
																	</div>

																
															</div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
                </div>

            )
        }
    }