import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class AddCourse extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Add Courses </h1> </div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
          <li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
              to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li><Link className="parent-item" to="/courses">Courses</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li className="active">Add Course</li>
        </ol>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>New Course</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">

                      <div className="form-group row">
       <label htmlFor="category" className="col-sm-2 col-form-label text-right">School *</label>
                                               <div className="col-sm-10">
                                                   <select className="form-control select21" id="school"
                                                           title="Select School..." name="school_id">
                                                       <option disabled selected>Select School</option>

                                                   </select>
                                               </div>
                     </div>

      <div className="form-group row">
       <label for="category" className="col-sm-2 col-form-label text-right">Department *</label>
                                               <div className="col-sm-10">
                                                   <select className="form-control select21" id="department"
                                                           title="Select Department..." name="department_id">
                                                       <option disabled selected>Select Department</option>


                                                   </select>
                                               </div>
                     </div>

      <div className="form-group row">
       <label for="title" className="col-sm-2 col-form-label text-right">Title : (*)
       </label>
         <div className="col-sm-10">
           <input id="title" name="title" type="text"
           placeholder="Title " className="form-control" required />
         </div>
            </div>

      <div className="form-group row">
       <label for="code" className="col-sm-2 col-form-label text-right">Code : (*)
       </label>
         <div className="col-sm-10">
           <input id="Code" name="code" type="text"
           placeholder="Code " className="form-control" required />
         </div>
            </div>

      <div className="form-group row">
       <label for="unit" className="col-sm-2 col-form-label text-right">Unit : (*)
       </label>
         <div className="col-sm-10">
           <input id="Unit" name="unit" type="text"
           placeholder="Unit " className="form-control" required />
         </div>
            </div>

      <div className="form-group row">
      <label for="course_type" className="col-sm-2 col-form-label text-right">Type : (*)
      </label>
      <div className="col-sm-10">
                              <select name="course_type" className="form-control form-control-lg" required>
                 <option value="0">Core/Elective</option>
                 <option value="1">Core</option>
                 <option value="2">Elective</option>
                 </select>
                           </div>
                          </div>

      <div className="form-group row">
       <label htmlFor="level" className="col-sm-2 col-form-label text-right">level : (*)
       </label>
         <div className="col-sm-10">
           <input id="level" name="level" type="text"
           placeholder="level " className="form-control" required/>
         </div>
            </div>

      <div className="form-group row">
                       <label className="col-sm-2 col-form-label text-right">Course Description</label>
                 <div className="col-sm-10">
                   <textarea className="form-control" rows="2" name="course_description" id="comment"></textarea>
                 </div>
             </div>

             <div className="form-group row">
      <label htmlFor="semester" className="col-sm-2 col-form-label text-right">Semester : (*)
      </label>
      <div className="col-sm-10">
                              <select name="semester" className="form-control form-control-lg" required>
                 <option value="0">First/Second</option>
                 <option value="1">First Semester</option>
                 <option value="2">Second Semester</option>
                 </select>
                           </div>
                          </div>



						<div id="button">
							<button type="submit" className="btn btn-info" >Submit </button>


						</div>


                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>
        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }