import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import 'summernote';
import 'bootstrap';
import { Link } from 'react-router-dom';

window.jquery = $;

const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class EmailCompose extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

	 componentDidMount() {
		$(document).ready(function() {
			$('#summernote').summernote();
		  });
	   }
   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">New Mail</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/email/inbox">Email Inbox</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">Compose</li>
							</ol>
						</div>
					</div>
					<div className="inbox">
						<div className="row">
							<div className="col-md-12">
								<div className="card card-topline-gray">
									<div className="card-body no-padding height-9">
										<div className="row">
											<div className="col-md-3">
												<div className="inbox-sidebar">
													<Link to="/email/compose" data-title="Compose"
														className="btn red compose-btn btn-block">
														<i className="fa fa-edit"></i> Compose </Link>
													<ul className="inbox-nav inbox-divider">
														<li><Link to="/email/inbox"><i
																	className="fa fa-inbox"></i> Inbox <span className="label mail-counter-style label-danger pull-right">2</span></Link>
																</li>
																<li><Link to="/email/mass-mail"><i className="fa fa-envelope"></i> Mass Mailing</Link>
                                                                </li>
																<li><Link to="/email/sent"><i className="fa fa-envelope"></i> Sent Mail</Link>
																</li>
																<li><Link to="/email/important"><i className="fa fa-briefcase"></i> Important</Link>
																</li>
																<li><Link to="/email/starred"><i className="fa fa-star"></i> Starred </Link>
																</li>
																<li><Link to="/email/draft"><i className=" fa fa-external-link"></i> Drafts
																		<span
																			className="label mail-counter-style label-info pull-right">30</span></Link>
																</li>
																<li><Link to="/email/trash"><i className="fa fa-trash-o"></i> Trash</Link>
																</li>
													</ul>

												</div>
											</div>
											<div className="col-md-9">
												<div className="inbox-body">
													<div className="inbox-header">
														<div className="mail-option">
															<div className="btn-group margin-top-20 ">
																<button
																	className="btn btn-primary btn-sm margin-right-10"><i
																		className="fa fa-check"></i> Send</button>
																<button
																	className="btn btn-sm btn-default margin-right-10"><i
																		className="fa fa-times"></i> Discard</button>
															</div>
															<div className="btn-group margin-top-20 ">
																<button
																	className="btn btn-sm btn-default margin-right-10">Draft</button>
															</div>
														</div>
													</div>
													<div className="inbox-body no-pad">
														<div className="mail-list">
															<div className="compose-mail">
																<form method="post">
																	<div className="form-group">
																		<label for="to" className="">To:</label>
																		<input type="text" tabindex="1" id="to"
																			className="form-control"/>
																		<div className="compose-options">
																			<a
																				href="#/">Cc</a>
																			<a
																				href="#/">Bcc</a>
																		</div>
																	</div> 
																	<div className="form-group hidden">
																		<label htmlFor="cc" className="">Cc:</label>
																		<input type="text" tabindex="2" id="cc"
																			className="form-control" />
																	</div>
																	<div className="form-group hidden">
																		<label htmlFor="bcc" className="">Bcc:</label>
																		<input type="text" tabindex="2" id="bcc"
																			className="form-control"/>
																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Subject:</label>
																		<input type="text" tabindex="1" id="subject"
																			className="form-control"/>
																	</div>
																	<div className="compose-editor">
																		<div id="summernote"></div>
																		<input type="file" className="default" multiple/>
																	</div>
																	<div className="btn-group margin-top-20 ">
																		<button
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Send</button>
																		<button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			Discard</button>
																	</div>
																	<div className="btn-group margin-top-20 ">
																		<button
																			className="btn btn-sm btn-default margin-right-10">Draft</button>
																	</div>
																</form>
															</div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }