import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class Chart extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className="pull-left">
          <div className="page-title">Dashboard</div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
          <li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
              to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li className="active">Dashboard</li>
        </ol>
      </div>
    </div>
   { /*<!-- start widget -->*/}
    <div className="state-overview">
      <div className="row">
        <div className="col-xl-3 col-md-6 col-12">
          <div className="info-box bg-b-green">
            <span className="info-box-icon push-bottom"><i className="material-icons">group</i></span>
            <div className="info-box-content">
              <span className="info-box-text">Total Students</span>
              <span className="info-box-number">450</span>
              <div className="progress">
                <div className="progress-bar" style={{width: "55%"}}></div>
              </div>
              <span className="progress-description">
                55% Increase in 28 Days
              </span>
            </div>
            {/*<!-- /.info-box-content -->*/}
          </div>
          {/*<!-- /.info-box -->*/}
        </div>
        {/*<!-- /.col -->*/}
        <div className="col-xl-3 col-md-6 col-12">
          <div className="info-box bg-b-yellow">
            <span className="info-box-icon push-bottom"><i className="material-icons">person</i></span>
            <div className="info-box-content">
              <span className="info-box-text">Total Schools</span>
              <span className="info-box-number">155</span>
              <div className="progress">
                <div className="progress-bar" style={{width: "40%"}}></div>
              </div>
              <span className="progress-description">
                40% Increase in 28 Days
              </span>
            </div>
            {/*<!-- /.info-box-content -->*/}
          </div>
         { /*<!-- /.info-box -->*/}
        </div>
        {/*<!-- /.col -->*/}
        <div className="col-xl-3 col-md-6 col-12">
          <div className="info-box bg-b-blue">
            <span className="info-box-icon push-bottom"><i className="material-icons">school</i></span>
            <div className="info-box-content">
              <span className="info-box-text">Total Staff</span>
              <span className="info-box-number">52</span>
              <div className="progress">
                <div className="progress-bar" style={{width: "85%"}}></div>
              </div>
              <span className="progress-description">
                85% Increase in 28 Days
              </span>
            </div>
            {/*<!-- /.info-box-content -->*/}
          </div>
          {/*<!-- /.info-box -->*/}
        </div>
        {/*<!-- /.col -->*/}
        <div className="col-xl-3 col-md-6 col-12">
          <div className="info-box bg-b-pink">
            <span className="info-box-icon push-bottom"><i
                className="material-icons">people</i></span>
            <div className="info-box-content">
              <span className="info-box-text">Total Users</span>
              <span className="info-box-number">13,921</span><span></span>
              <div className="progress">
                <div className="progress-bar" style={{width: "50%"}}></div>
              </div>
              <span className="progress-description">
                50% Increase in 28 Days
              </span>
            </div>
            {/*<!-- /.info-box-content -->*/}
          </div>
          {/*<!-- /.info-box -->*/}
        </div>
        {/*<!-- /.col -->*/}
      </div>
    </div>
    {/*<!-- end widget -->*/}
    {/*<!-- chart start -->*/}
    <div className="row">
      <div className="col-sm-6">
        <div className="card card-box">
          <div className="card-head">
            <header>Income Survey</header>
            <div className="tools">
              <a className="fa fa-repeat btn-color box-refresh" href="#/"> </a>
              <a className="t-collapse btn-color fa fa-chevron-down" href="#/"> </a>
              <a className="t-close btn-color fa fa-times" href="#/"> </a>
            </div>
          </div>
          <div className="card-body">
            <div className="recent-report__chart">
              <div id="chart1"></div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-sm-6">
        <div className="card card-box">
          <div className="card-head">
            <header>Expenditure Survey</header>
            <div className="tools">
              <a className="fa fa-repeat btn-color box-refresh" href="#/"> </a>
              <a className="t-collapse btn-color fa fa-chevron-down" href="#/"> </a>
              <a className="t-close btn-color fa fa-times" href="#/"> </a>
            </div>
          </div>
          <div className="card-body">
            <div className="recent-report__chart">
              <div id="chart2"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
   { /*<!-- Chart end -->*/}


    {/*<!-- end new student list -->*/}
  </div>
</div>

            )
        }
    }