import React, { Component } from 'react';
import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';
import data from './constants'


const Loading = (props) => (
  <div
  style={{
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center" 
  }}
>
  <Loader type="Circles" color="#FFFFFF" height="40" width="40" />
  <div className="text-center p-t-30">
          <p className="txt1">
            Please wait...
          </p>
        </div>
</div>
//
)

export default class EditAdminType extends Component {
     constructor(props) {
         super(props);
         this.onChangeTitle = this.onChangeTitle.bind(this);
         this.onChangeCode = this.onChangeCode.bind(this);
         this.onChangeDescription = this.onChangeDescription.bind(this);
         this.onSubmit = this.onSubmit.bind(this);
 
         this.state = {
             title: '',
             description: '',
             email: '',
             code: '',
             error:'',
             button: 'Send Message',
             loading: false,
             display: 'none'
         }
     } 

     componentDidMount() {

      axios.get(`${data.host}api/v1/admin-types/edit/${this.props.match.params.id}?token=${data.token}`)
      .then(response => {
       return response.data.length !== 0? this.setState({ title: response.data[0].title, description: response.data[0].description, code: response.data[0].title, loading: false, display: 'block' }) : this.setState({error: 'This record does not exit'});
      })
      .catch((error) => {
        console.log(error);
      });
  
     }
     onChangeCode(e) {
      this.setState({code: e.target.value});
    }
 
    onChangeTitle(e) {
     this.setState({title: e.target.value});
   }
 
   onChangeDescription(e) {
     this.setState({description: e.target.value});
   }
     onSubmit(e) {
      e.preventDefault()
      this.setState({loading: true, error: ''});
  
      const details = {
        title: this.state.title,
        code: this.state.code,
        description: this.state.description,
        id: this.props.match.params.id
      }
  
      axios.post(`${data.host}api/v1/admin-types/update?token=${data.token}`, details)
        .then(res => {
          window.history.back();
        }).catch(err => {
          this.setState({ loading: false, error: err.toString() });
        });
  
    }
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className="pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Edit Admin Type </h1> </div>
        </div>

        
        <ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>

                <li className="active"> <Link className="parent-item" to="/admin-types"> Admin Types </Link><i className="fa fa-angle-right"></i> </li>
                <li className="active">Edit Admin Type</li>
							</ol>


      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>Editing</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">

                    <form style={{"display": this.state.display}} onSubmit={this.onSubmit}>
                      <div className="form-group row">
                        
              				<label htmlFor="title" className="col-sm-2 col-form-label text-right">Title :
              				</label>
              					<div className="col-sm-10">
              						<input onChange={this.onChangeTitle} value={this.state.title} id="title" name="title" type="text"
              						placeholder="Title " className="form-control" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label htmlFor="code" className="col-sm-2 col-form-label text-right">Name :
              				</label>
              					<div className="col-sm-10">
              						<input onChange={this.onChangeCode} value={this.state.code}  id="Code" name="code" type="text"
              						placeholder="Code " className="form-control required" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label htmlFor="email" className="col-sm-2 col-form-label text-right">Description :
              				</label>
              					<div className="col-sm-10">
              						<textarea onChange={this.onChangeDescription} value={this.state.description} name="description" rows="8"  className="form-control"></textarea>
              					</div>
                      </div>
                                 

                                  <div id="button">

                                   { this.state.loading? this.Loaderview() : <button type="submit" className="btn btn-info" >Save </button>}


                                  </div>

            </form>

                                <div className="text-center p-t-30">
                                  <p className="txt1">
                                    {this.state.error}
                                  </p>
                                </div>

                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>


        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }