import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import 'summernote';
import 'bootstrap';
import { Link } from 'react-router-dom';

window.jquery = $;
const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class MessageCompose extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    
   componentDidMount() {
	$(document).ready(function() {
		$('#summernote').summernote();
	  });
   }

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">Compose</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/messages/inbox">Message Inbox</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">Compose</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12">
							<div className="card card-topline-gray">
								<div className="card-body no-padding height-9">
									<div className="inbox">
										<div className="row">
											<div className="col-md-3">
												<div className="inbox-sidebar">

													<ul className="inbox-nav inbox-divider">
														<li><Link to="/messages/inbox"><i
																	className="fa fa-inbox"></i> Inbox <span
																	className="label mail-counter-style label-danger pull-right">2</span></Link>
														</li>
														<li><Link to="/messages/sent"><i className="fa fa-envelope"></i> Sent
															<span
																className="label mail-counter-style label-info pull-right">0</span></Link>
														</li>
														<li><Link to="/messages/draft"><i className=" fa fa-external-link"></i> Drafts
																<span
																	className="label mail-counter-style label-info pull-right">0</span></Link>
														</li>
														<li><Link to="/messages/trash"><i className=" fa fa-trash-o"></i> Trash
															<span
																className="label mail-counter-style label-info pull-right">0</span></Link>
														</li>
													</ul>


												</div>
											</div>
											<div className="col-md-9">
												<div className="inbox-body">
													<div className="inbox-body no-pad">

															<div className="compose-mail">
																<form method="post">
																	<div className="form-group">
																		<label for="to" className="">School:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select School</option>
																			<option value="1">COE</option>
																			<option value="2">IBBUL</option>
																			<option value="3">NSUK</option>
																			<option value="4">FUT Mx</option>
																			<option value="5">School of Health</option>
																		</select>

																	</div>
																	<div className="form-group">
																		<label htmlFor="to" className="">Receiver Type:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select Receriver type</option>
																			<option value="1">Superadmin</option>
																			<option value="2">Admin</option>
																			<option value="3">School</option>
																			<option value="4">Staff</option>
																			<option value="5">Students</option>
																		</select>

																	</div>
																	<div className="form-group">
																		<label htmlFor="to" className="">Receiver:</label>
																		<select className="form-control" name="school">
																			<option value="0">Select Receriver</option>
																			<option value="1">Abbas</option>
																			<option value="2">Aslapai</option>
																			<option value="3">John</option>
																			<option value="4">Ezra</option>
																			<option value="5">Adamu</option>
																		</select>

																	</div>
																	<div className="form-group">
																		<label htmlFor="subject" className="">Subject:</label>
																		<input type="text" tabindex="1" id="title"
																			className="form-control"/>
																	</div>

																	<div className="compose-editor">
																			<label for="subject" className="">Message:</label>
																		<div id="summernote"></div>
																	</div>

																	<div className="btn-group margin-top-20 ">
																		<button
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Send</button>
																		<button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			Discard</button>
																	</div>
																	<div className="btn-group margin-top-20 ">
																		<button
																			className="btn btn-sm btn-default margin-right-10">Draft</button>
																	</div>

																</form>
															</div>
														</div>

												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }