import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class AddModule extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Add Modules </h1> </div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>

                <li className="active"> <Link className="parent-item" to="/modules"> Modules </Link><i className="fa fa-angle-right"></i> </li>
                <li className="active">Add Module</li>
							</ol>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>New Module</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">

                      <div className="form-group row">
              				<label htmlFor="title" className="col-sm-2 col-form-label text-right">Name :
              				</label>
              					<div className="col-sm-10">
              						<input id="title" name="title" type="text"
              						placeholder="name " className="form-control required" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label htmlFor="code" className="col-sm-2 col-form-label text-right">Purpose :
              				</label>
              					<div className="col-sm-10">
              						<input id="Code" name="code" type="text"
              						placeholder="Purpose " className="form-control required" required />
              					</div>
                      </div>
                      <div className="form-group row">
              				<label htmlFor="email" className="col-sm-2 col-form-label text-right">Activity :
              				</label>
              					<div className="col-sm-10">
              						<input id="email" name="email" type="text"
              						placeholder="Activity " className="form-control" required />
              					</div>
                      </div>


              			 <div className="form-group row">
              				<label htmlFor="location" className="col-sm-2 col-form-label text-right">Github url:
              				</label>
              					<div className="col-sm-10">
              						<input id="text" name="location" type="text"
              						placeholder="ex. www.github.com/abbas " className="form-control" required  />
              				</div>
                    </div>

            			 <div className="form-group row">
            				<label htmlFor="Phone Number" className="col-sm-2 col-form-label text-right">Current Version:
            				</label>
            					<div className="col-sm-10">
            						<input id="text" name="pn" type="text"
            						placeholder="ex. version 1.0 " className="form-control" required  />
            				</div>
                  </div>
                  <div className="form-group row">
                   <label htmlFor="Phone Number" className="col-sm-2 col-form-label text-right">Number of API:
                   </label>
                     <div className="col-sm-10">
                       <input id="number_api" name="pn" type="number"
                       placeholder="ex. 2 " className="form-control" required  />
                   </div>
                 </div>
                 <div className="form-group row">
                  <label htmlFor="Phone Number" className="col-sm-2 col-form-label text-right">Status:
                  </label>
                    <div className="col-sm-10">
                      <select className="form-control" name="status">
                        <option value="0">---Select</option>
                        <option value="1">Activate</option>
                        <option value="2">Deactivate</option>
                      </select>
                  </div>
                </div>


						<div id="button">
						
														<button type="submit" className="btn btn-info" >Submit </button>

                            	
						</div>


                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>


        </div>
      </div>
    </div>
  </div>
</div>

            )
        }
    }