import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import $ from 'jquery';
import JSZip from 'jszip';
import pdfMake from 'pdfmake';
import pdfFonts from "pdfmake/build/vfs_fonts";
import 'datatables.net-bs4';
import 'datatables.net-responsive';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.colVis'
import 'datatables.net-buttons/js/buttons.html5'
import 'datatables.net-buttons/js/buttons.flash'
import 'datatables.net-buttons/js/buttons.print'
import { Link } from 'react-router-dom';

window.JSZip = JSZip;

pdfMake.vfs = pdfFonts.pdfMake.vfs;

const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class Types extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

    
     componentDidMount() {
	    $(document).ready(function () {
        $('#exportTable').DataTable({
          lengthChange: true,
        dom: 'Bfrtip',
        buttons: [
            'copy', 'csv', 'excel', 'pdf', 'print'
        ]
		
    });
		});
   }
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">School Lists</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>

								<li className="active">Types</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12 col-sm-12">
							<div className="card card-box">
								<div className="card-head">
									<header>All School</header>
									<button id="panel-button"
										className="mdl-button mdl-js-button mdl-button--icon pull-right"
										data-upgraded=",MaterialButton">
										<i className="material-icons">more_vert</i>
									</button>
									<ul className="mdl-menu mdl-menu--bottom-right mdl-js-menu mdl-js-ripple-effect"
										data-mdl-for="panel-button">
										<li className="mdl-menu__item"><i className="material-icons">assistant_photo</i>Action
										</li>
										<li className="mdl-menu__item"><i className="material-icons">print</i>Another action
										</li>
										<li className="mdl-menu__item"><i className="material-icons">favorite</i>Something else
											here</li>
									</ul>
								</div>
								<div className="row">
									<div className="col-md-6 col-sm-6 col-6">
										<div className="btn-group" style={{marginLeft:"20px"}}>
											<Link to="/types/add-type" id="addRow" className="btn btn-info" >
												Add New <i className="fa fa-plus"></i>
											</Link>
										</div>
									</div>

								</div>
								<div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
									<form className="form-inline">
                  <div style={{marginRight:"-25px"}}>
                    <div className="col-md-12">
                    Schools
                    </div>
                      <div className="col-md-12">
                    <select className="form-control" name="school" style={{width:"600px", margin:"0px"}}>
                      <option value="0">select school</option>
                      <option value="1">IBBUL</option>
                      <option value="2">NSUK</option>
                      <option value="3">COE</option>
                      <option value="4">FUT Mx</option>
                    </select>
                  </div>
                  </div>


                <div style={{marginRight:"-25px"}}>

                    <div className="col-md-12" style={{marginTop:"25px"}}>
                <button type="submit" className="btn btn-default" style={{width:"80px"}}>Search</button>
                </div>
              </div>

                </form>
									<table id="exportTable" className="display nowrap" style={{width:"100%"}}>
										<thead>
											<tr>
                        <td></td>
												<th>TITLE</th>
												<th>CODE</th>
												<th>EMAIL</th>
												<th>PHONE NUMBER</th>
												<th>DATE ADDED</th>
												<th>ACTION</th>
											</tr>
										</thead>
										<tbody>


											<tr>
                        <td>1</td>
												<td>Lael Greer</td>
												<td>Systems Administrator</td>
												<td>London</td>
												<td>21</td>
												<td>2009/02/27</td>
                        <td>
                         <Link to="/types/edit-type/90885"
                            className="btn btn-primary btn-xs edit_school">
                            <i className="fa fa-pencil"></i>
                          </Link>
                          <button className="btn btn-danger btn-xs delete_school">
                            <i className="fa fa-trash-o "></i>
                          </button>
                        </td>
											</tr>

											<tr>
                        <td>2</td>
												<td>Donna Snider</td>
												<td>Customer Support</td>
												<td>New York</td>
												<td>27</td>
												<td>2011/01/25</td>
                        <td>
                          <Link to="/types/edit-type/985"
                            className="btn btn-primary btn-xs edit_school">
                            <i className="fa fa-pencil"></i>
                          </Link>
                          <button className="btn btn-danger btn-xs delete_school">
                            <i className="fa fa-trash-o "></i>
                          </button>
                        </td>
											</tr>
										</tbody>
									
									</table>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>

            )
        }
    }