import React, { Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import JSZip from 'jszip';
import pdfMake from 'pdfmake';
import pdfFonts from "pdfmake/build/vfs_fonts";
import 'datatables.net-bs4';
import 'datatables.net-responsive';
import 'datatables.net-buttons-bs4';
import 'datatables.net-buttons/js/buttons.colVis'
import 'datatables.net-buttons/js/buttons.html5'
import 'datatables.net-buttons/js/buttons.flash'
import 'datatables.net-buttons/js/buttons.print'
import { Link } from 'react-router-dom';
import data from './constants';
import Loading from './loader';

window.JSZip = JSZip;

pdfMake.vfs = pdfFonts.pdfMake.vfs;


export default class Documentation extends Component {
     constructor(props) {
         super(props);
       
 
		 this.onChangeSchool = this.onChangeSchool.bind(this);
		 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
			 loading: true,
			 documentations: [],
			 schools: []
         }
     } 

   
     componentDidMount() {
		axios.get(`${data.host}api/v1/documentations?token=${data.token}`)
		.then(response => {
			
			this.setState({ documentations: response.data, loading: false });

			$(document).ready(() => {
			$('#exportTable').DataTable({
					lengthChange: true,
					dom: 'Bfrtip',
					data: response.data,
					columns: [
						{ data: "id" },
						{ data: "title" },
						{ data: "added_by" },
						{ data: "note" },
						{ data: "version" },
						{ data: "date_added" },
						{ data: "id",
						render:  ( id ) => {
							return "<a href='/documentations/edit-documentation/"+id+"' class='btn btn-primary btn-xs' title='Edit record'><i class='fa fa-pencil'></i></a> | <a href='/documentations/delete-documentation/"+id+"' class='btn btn-primary btn-xs' title='Delete record'><i class='fa fa-trash-o'></i></a>";
						  }}
					  ],
					buttons: [
						'copy', 'csv', 'excel', 'pdf', 'print'
					]
	
				});
				
			});
	
		})
		.catch((error) => {
			this.setState({ error: error.toString(), loading: false });
		});

		axios.get(`${data.host}api/v1/schools?token=${data.token}`)
      .then(response => {
        this.setState({schools: response.data})
      })
      .catch((error) => {
        console.log(error);
      });
	}

		onChangeSchool(e) {
			// eslint-disable-next-line
		   $('#exportTable').dataTable().fnDestroy();
		   if (e.target.value !== '0000') {
			$('#exportTable').DataTable({
				lengthChange: true,
				dom: 'Bfrtip',
				data: this.state.documentations.filter(d => d.school_id.toString() === e.target.value),
				columns: [
					{ data: "id" },
					{ data: "title" },
					{ data: "added_by" },
					{ data: "note" },
					{ data: "version" },
					{ data: "date_added" },
					{ data: "id",
					render:  ( id ) => {
						return "<a href='/documentations/edit-documentation/"+id+"' class='btn btn-primary btn-xs' title='Edit record'><i class='fa fa-pencil'></i></a> | <a href='/documentations/delete-documentation/"+id+"' class='btn btn-primary btn-xs' title='Delete record'><i class='fa fa-trash-o'></i></a>";
					  }}
				  ],
				buttons: [
					'copy', 'csv', 'excel', 'pdf', 'print'
				]

			});
		   }else{
			$('#exportTable').DataTable({
				lengthChange: true,
				dom: 'Bfrtip',
				data: this.state.documentations,
				columns: [
					{ data: "id" },
					{ data: "title" },
					{ data: "added_by" },
					{ data: "note" },
					{ data: "version" },
					{ data: "date_added" },
					{ data: "id",
					render:  ( id ) => {
						return "<a href='/documentations/edit-documentation/"+id+"' class='btn btn-primary btn-xs' title='Edit record'><i class='fa fa-pencil'></i></a> | <a href='/documentations/delete-documentation/"+id+"' class='btn btn-primary btn-xs' title='Delete record'><i class='fa fa-trash-o'></i></a>";
					  }}
				  ],
				buttons: [
					'copy', 'csv', 'excel', 'pdf', 'print'
				]

			});
		}
   }

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className=" pull-left">
								<div className="page-title">Documentation Manager</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
							
								<li className="active">Documentation</li>
							</ol>
						</div>
					</div>
					<div className="row">
						<div className="col-md-12 col-sm-12">
							<div className="card card-box">
								<div className="card-head">
									<header>All Documentations</header>

								</div>
								<div className="row">
									<div className="col-md-6 col-sm-6 col-6">
										<div className="btn-group">
											<Link to="/documentations/add-documentation" id="addRow" className="btn btn-info" >
												Add New <i className="fa fa-plus"></i>
											</Link>
										</div>
									</div>


								<div className="card-body " id="bar-parent" style={{overflow:"auto"}}>
										<form className="form-inline">
											<div>
												<div className="col-md-12">
													Schools
                    </div>
												<div className="col-md-12">
													<select onChange={this.onChangeSchool} className="form-control" name="school" style={{ width: "600px", margin: "0px;" }}>
														<option value="0000">All Documentations</option>
														<option value="0">Control house</option>
														{this.state.schools.map((school) => <option value={school.id}>{school.title}</option>)}
													</select>
												</div>
											</div>



										</form>
										<table id="exportTable" className="user display table-bordered table-hover table-striped" cellSpacing="0" width="100%" style={{fontSize:"12px"}}>
										<thead>
											<tr>
                        <td>ID</td>
												<th>NAME</th>
												<th>EDITOR</th>
												<th>NOTE</th>
												<th>VERSION</th>
												<th>DATE ADDED</th>
												<th>ACTION</th>
											</tr>
										</thead>
										
										
									</table>
									<div className="text-center p-t-30">
										<p className="txt1">
											{this.state.error}
										</p>
									</div>
									{this.Loaderview()}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
	</div>

            )
        }
    }