import React, { Component } from 'react';
//import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link } from 'react-router-dom';


const Loading = (props) => (
    <div
    style={{
      width: "100%",
      height: "100%",
      position: "absolute",
      zIndex: "1",
      left: "0",
      top: "0",
      justifyContent: "center",
      alignItems: "center",
      textAlign: "center"
    }}
  >
    <Loader type="ThreeDots" color="#875AFE" height="80" width="80" />
  </div>
  //
)

export default class EditProgrammeCourses extends Component {
     constructor(props) {
         super(props);
        /* this.onChangeName = this.onChangeName.bind(this);
         this.onChangePhone = this.onChangePhone.bind(this);
         this.onChangeEmail = this.onChangeEmail.bind(this);
         this.onChangeMessage = this.onChangeMessage.bind(this);
         this.onSubmit = this.onSubmit.bind(this); */
 
         this.state = {
             name: '',
             phone: '',
             email: '',
             message: '',
             error:'',
             button: 'Send Message',
             loading: false
         }
     } 

   
    

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
  <div className="page-content">
    <div className="page-bar">
      <div className="page-title-breadcrumb">
        <div className=" pull-left">
          <div className="page-title"><h1 style={{marginBottom:"-50px"}}>Edit Programme Courses </h1> </div>
        </div>
        <ol className="breadcrumb page-breadcrumb pull-right">
          <li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
              to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li><Link className="parent-item" to="/programme-courses">Programme Courses</Link>&nbsp;<i className="fa fa-angle-right"></i>
          </li>
          <li className="active">Edit Programme Course</li>
        </ol>
      </div>
    </div>
    <div className="row">
      <div className="col-md-12">
        <div className="tabbable-line">


          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-box">
                    <div className="card-head">
                      <header>Editing</header>
                      <div className="tools">
                        <a className="fa fa-repeat btn-color box-refresh"
                          href="#/"> </a>
                        <a className="t-collapse btn-color fa fa-chevron-down"
                          href="#/"> </a>
                        <a className="t-close btn-color fa fa-times"
                          href="#/"> </a>
                      </div>
                    </div>
                    <div className="card-body ">
                      <div className="col-md-12">
                        <div className="col-md-8" style={{float:"left"}}>
                          <fieldset>
                                  <legend><span className="ast">*</span> indicate required fields </legend>




                                <div className="form-group">
                                    <label className="col-sm-3 control-label" for="level">Level</label>
                                    <div className="col-md-12">
                                       <div className="input-group">

                                      <select  className="form-control input-sm" name="level"  id="level">
                                        <option value="0" >-- Choose Year --</option>
                                        <option value="100">Year One</option>
                                        <option value="200">Year Two</option>
                                        <option value="300">Year Three</option>
                                        <option value="400">Year Four</option>

                                        <option value="500">Year Five</option>
                                        <option value="600">Year Six</option>

                                      </select>
                                    <div className="input-group-addon"><span className="ast">*</span></div>

                                        </div>

                                    </div>

                                  </div>


                                   <div className="form-group">
                                    <label className="col-sm-3 control-label" htmlFor="programme_type">Programme Type</label>
                                    <div className="col-md-12">
                                      <div className="input-group">

                                      <select id="search_prog_type_id" className="form-control input-sm" name="programme_type" required>
                                      <option value="0">---select</option>
                                      </select>


                                          <div className="input-group-addon"><span className="ast">*</span></div>

                                        </div>

                                         <span className="help-block">Diploma / Advance Diploma or Cert</span>
                                    </div>

                                  </div>


                                 <div className="form-group">
                                <label className="col-sm-3 control-label" htmlFor="search_department_id">Department</label>
                                <div className="col-md-12">

                                  <div  className="input-group">
                                  <select  className="form-control input-sm" name="search_department"  id="search_department_id" >
                                   <option value="0"> ---- Choose Department --- </option>


                                   </select>

                                        <div className="input-group-addon"><span className="ast">*</span></div>

                                        </div>
                                    </div>

                                </div>

                                <div className="form-group" id="show_programme">
                                <label className="col-sm-3 control-label" for="programme_id">Programme</label>
                                  <div  className="col-md-12" id="hide_sub_programme" >

                                  
                                     <div className="input-group">
                                          <select   name="sub_programme"  id="sub_programme_id" className="form-control input-sm">

                                          <option value="0">--Choose Programme --</option>

                                          </select>

                                        <div className="input-group-addon"><span className="ast">*</span></div>

                                        </div>
                                  </div>


                                   <div  className="col-md-12" id="show_sub_programme" >



                                  </div>

                                </div>



                                 <div className="form-group">
                                <label className="col-md-12 control-label" for="courses">Choose courses from departments / levels</label>
                                  <div className="col-md-12" style={{minHeight:"200px", minWidth:"200px", height:"auto"}}>



                                    <br /><br />
                                  <div className="input-group">
                                    <div className="btn btn-info" style={{height:"30px"}}> Level <span className="ast">*</span></div>
                                    <select  className="form-control input-sm" name="level2"  id="levl">
                                        <option value="0" >-- Choose Year --</option>
                                        <option value="100">Year One</option>
                                        <option value="200">Year Two</option>
                                        <option value="300">Year Three</option>

                                         <option value="400">Year Four</option>

                                        <option value="500">Year Five</option>
                                        <option value="600">Year Six</option>
                                    </select>

                                    </div>   


                                    <br />

                                      <div className="input-group">
                                      <div className="btn btn-info" style={{height:"30px"}}> Department <span className="ast">*</span></div>

                                        <select  className="form-control input-sm" name="department2"  id="search_department_id2" >
                                         <option value="0"> </option>


                                         </select>

                                      </div>

                                      <br />
                                     

                                      <div id="show_courses">

                                      </div>


                                     <span className="help-block">Here select level and department you want to pick a course from, then check the courses</span>
                                  </div>


                                  <td width='5%'><input type='checkbox' name='course[]' value="" /></td>
             <td width='75%'> csc101  </td>
                 <td width='20%'>

                 <div className='btn-group' data-toggle='buttons' >
                   <label className='btn btn-primary btn-small' >
                       <input id='option1' value='1'  type='radio' name='seen_as_elective'  autocomplete='off'  />Yes

                   </label>

                   <label className='btn btn-primary btn-small active' >
                       <input id='option2' value='0' type='radio' name='seen_as_elective'  autocomplete='off' checked /> No

                   </label>

                 </div>



                 </td>






                                </div>





                           <div className="form-group">
                                <label className="col-sm-2 control-label" for="gender">Status</label>
                                <div className="col-md-12">

                                  <select id="status" className="form-control input-sm" name="status" required>

                                  <option value="0">---Select</option>

                                   </select>


                                </div>

                              </div>
                              <button type="button" name="button" className="btn btn-primary">Submit</button>
                              
                        </fieldset>
                        </div>
                        <div className="col-md-4" style={{float:"right"}}>
                          <h4>Instructions on how to add  courses to programmes /level (courses to offer)</h4>

                         <ol className="list-group">
                         <li className="list-group-item">1-  First level, is the level that you want to set the courses <b>FOR</b></li>
                         <li className="list-group-item">2 - First department is the department you want to set the courses <b>FOR</b> </li>
                         <li className="list-group-item">3 - Programme is the programme you want to set the courses <b>FOR</b> </li>



                           <ul className="list-list-group">


                              <li className="list-group-item"> 3a- Second level is the level you want to choose the course(s) <b>FROM</b>
                              </li>

                              <li className="list-group-item"> 3b- Second department is the department you want to choose the course(s) <b>FROM</b></li>
                              <li className="list-group-item"> 3c- Check list all the courses you want to attach as (courses to offer by a particular programme per level)
                              </li>

                               <li className="list-group-item"> 3d- Finally, by the right hand side of every course, there is a button to switch yes,
                                if a course is taken as an elective in every attachments.
                              </li>

                             </ul>

                        

                         <li className="list-group-item">4- Users must beware of status functionality - "when to publish and when not to publish".</li>
                        
</ol>
                        </div>







                    </div>
                    <div className="col-lg-4" style={{float:"right"}}>
                        <br /> <br />


              <input type="hidden" value="save_programmes_courses" name="act" />



                    </div>

                      </div>




                    </div>
                    </div>
                  </div>
                </div>




              </div>
            </div>

        </div>
      </div>
    </div>
  </div>


            )
        }
    }