import React, { Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import data from './constants';
import Loading from './loader';

window.jquery = $;



export default class ActivateAPI extends Component {
     constructor(props) {
         super(props);
		 this.onChangeTitle = this.onChangeTitle.bind(this);
         this.onChangePurpose = this.onChangePurpose.bind(this);
		 this.onChangeUrl = this.onChangeUrl.bind(this);
		 this.onChangeHost = this.onChangeHost.bind(this);
		 this.onChangeVersion = this.onChangeVersion.bind(this);
		 
         this.onSubmit = this.onSubmit.bind(this); 
 
         this.state = {
             title: '',
             purpose: '',
             url: '',
			 host: '',
			 version: '',
             error:'',
             button: 'Send Message',
             loading: false,
             display: 'none'
         }
     } 

   
	 componentDidMount() {

		axios.get(`${data.host}api/v1/apis/edit/${this.props.match.params.id}?token=${data.token}`)
		.then(response => {
		 return response.data.length !== 0? this.setState({ title: response.data[0].title, purpose: response.data[0].purpose, host: response.data[0].host, url: response.data[0].url, version: response.data[0].version, loading: false, display: 'block' }) : this.setState({error: 'This record does not exit'});
		})
		.catch((error) => {
		  console.log(error);
		});
	
	   }


		onChangePurpose(e) {
			this.setState({purpose: e});
		  }
	   
		  onChangeTitle(e) {
		   this.setState({title: e.target.value});
		 }
	   
		 onChangeUrl(e) {
		   this.setState({url: e.target.value});
		 }

		 onChangeHost(e) {
			this.setState({host: e.target.value});
		  }

		  onChangeVersion(e) {
			this.setState({version: e.target.value});
		  }
	   
		 onSubmit(e) {
		   
		   this.setState({loading: true, error: ''});
	   
		   const details = {
			 id: this.props.match.params.id
		   }
	   
		   axios.post(`${data.host}api/v1/apis/deactivate?token=${data.token}`, details)
			 .then(res => {
			   window.location = '/api-manager';
			 }).catch(err => {
			   this.setState({ loading: false, error: err.toString() });
			 });
	   
		 }

     Loaderview() {
        return this.state.loading? <Loading /> : null
        
    }

     render() {
        return (
           <div className="page-content-wrapper">
				<div className="page-content">
					<div className="page-bar">
						<div className="page-title-breadcrumb">
							<div className="pull-left">
								<div className="page-title">Deactivate API</div>
							</div>
							<ol className="breadcrumb page-breadcrumb pull-right">
								<li><i className="fa fa-home"></i>&nbsp;<Link className="parent-item"
										to="/">Home</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li><Link className="parent-item" to="/api-manager">API Manager</Link>&nbsp;<i className="fa fa-angle-right"></i>
								</li>
								<li className="active">Deactivate API</li>
							</ol>
						</div>
					</div>
					<div className="inbox">
						<div className="row">
							<div className="col-md-12">
								<div className="card card-topline-gray">
									<div className="card-body no-padding height-9">
										<div className="row">

											<div className="col-md-12">
												<div className="inbox-body">

													<div className="inbox-body no-pad">

															<div className="compose-mail">
                                                            <div className="text-center p-t-30">
																	<h2 className="txt1">
																		Are you sure you want to deactivate this API?
																	</h2>
																	<div className="text-center p-t-30">
																	<p className="txt1">
																		{this.state.error}
																	</p>
																</div>
																	<div className="btn-group margin-top-20 ">
																		{ this.state.loading? this.Loaderview() :<> <button onClick={this.onSubmit}
																			className="btn btn-primary btn-sm margin-right-10"><i
																				className="fa fa-check"></i> Yes</button>
																		<Link to="/api-manager"><button
																			className="btn btn-sm btn-default margin-right-10"><i
																				className="fa fa-times"></i>
																			No</button> </Link> </>}
																	</div>

																
															</div>
														</div>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
                </div>

            )
        }
    }